.alert {
  font-size: small;
}

.app {
  text-align: center;
  color: white;
  min-height: 100vh;
  background-color: #282c34;
}

.app-header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: small;
  padding: 1rem;
  flex-wrap: wrap;
}

.left-header {
  display: flex;
  align-items: center;
  margin-right: auto;
}

.app-body {
  align-items: center;
  font-size: calc(10px + 2vmin);
  max-width: 40rem;
  margin: auto;
}

.text-blob {
  padding: 1rem;
  text-align: justify;
}

.info-link {
  font-size: small;
  margin: 1rem;
}

img {
  padding: 1rem;
}

h3 {
  padding: 1rem;
}

button {
  margin: 1rem;
}

input {
  max-width: 20rem;
  margin: 1rem;
}

label {
  font-size: medium;
  margin: 1rem;
  min-width: 5rem;
  max-width: 5rem;
}

.form-group {
  display: flex;
  margin: auto;
  justify-content: center;
}

.error {
  font-size: small;
  color: red;
  max-width: 26rem;
}

.link {
  margin: 0;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: small;
  border: none;
  background: none;
  box-shadow: none;
  padding: 0;
  color: lightblue;
}
.link:hover {
  border: none;
  background: none;
  box-shadow: none;
}
.link:active {
  border: none !important;
  background: none !important;
  box-shadow: none !important;
}

.dropdown-toggle {
  border: none !important;
  background: none !important;
  box-shadow: none !important;
}

.declaration {
  justify-content: center;
  padding: 1rem;
  display: flex;

  .input-group-text {
    background: none;
    border: none;
  }

  .text {
    font-size: medium;
    text-align: justify;
  }
}

.info-table {
  display: inline;
  font-size: medium;
  color: white;
  text-align: left;
}